.container {
  background: #f2f2f2;
  height: 100vh;
  width: 100%;
}

div.header {
  display: flex;
  justify-content: space-around;
  align-items: center;

  padding: 20px;

  border-bottom: 1px solid #ddd;
}

button.adicionar {
  padding: 10px;
  border-radius: 4px;
  outline: none;
  border: none;

  background: #b96e3d;
  color: white;
}

button.adicionar:hover {
  background: #cb895e;
}

.section {
  padding: 20px;
  margin-top: 15px;
  width: 100vw;
}

.cardContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 15px;
}
@media (max-width: 1085px) {
  .cardContainer {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (max-width: 810px) {
  .cardContainer {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 550px) {
  .cardContainer {
    grid-template-columns: 1fr;
  }
}

.cards {
  display: flex;
  flex-direction: column;

  width: 250px;
  height: 200px;

  padding: 10px;

  background: #fff;

  box-shadow: 5px 2px 6px rgba(0, 0, 0, 0.1);
}

.cards-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h2 {
  margin: 10px 0 20px;
}

.deletar {
  border: none;
  background: none;
  outline: none;

  cursor: pointer;

  color: red;
}
.deletar:hover {
  color: rgba(255, 0, 0, 0.6);
}
